<template>

  <!-- <router-link :to="{ price: 'show', params: { name: stuff.name }}"> -->
  <!-- </router-link> -->
  <!-- <router-link :to="{ price: 'show', params: { name: stuff.name }}" class="link">자세히보기</router-link> -->
  <b-container fluid="lg" id="wrapper">
      <!-- nav -->
        <ul class="nav nav-tabs">
            <li class="nav-item">
                <a class="nav-link" href="/">홈화면 <span class="sr-only">(current)</span></a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="#">일과기록</a>
            </li>
            <li class="nav-item">
                <a class="nav-link active" href="/mari">마리상점</a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="/collection">수집형 포인트</a>
            </li>
        </ul>

          <!-- <p style="color:white">{{stuffs}}</p> -->
          <b-container fluid="lg" class="border-circle bg-dark">
            <a target="_sub" href="https://lostark.game.onstove.com/Shop/Mari" class="btn btn-secondary">마리상점 구매링크</a>
          </b-container>
    <!-- 거래소 크리스탈 가격 -->
    <b-container fluid="lg" class="border-circle bg-dark">
      <legend style="color: white;">화폐 거래소 시세</legend>
      <b-row class="b-form-row">
        <b-col sm="4" class="b-form-group">
          <label for="gold_to_cash_loyal" class="">로얄 크리스탈</label>
          <input type="text" class="b-form-control input_white" id="gold_to_cash_loyal" value="2612.5" disabled>
        </b-col>
        <b-col sm="4" class="b-form-group">
          <label for="gold_to_cash_crystal">크리스탈</label>
          <input type="text" class="b-form-control input_white" id="gold_to_cash_crystal" value="95" disabled>
        </b-col>
        <b-col sm="4" class="b-form-group">
          <label for="gold_to_cash_gold">골드</label>
          <input type="number" class="b-form-control input_white" id="gold_to_cash_gold"
          v-on:blur="changeMari($event)" v-on:keyup.enter="changeMari($event)"  v-bind:value="mariGold">
        </b-col>
      </b-row>
    </b-container>
    <!-- 버튼 하나 만들어서 설정 저장? -->
    <!-- 마리 상점 -->
    <table class="table table-dark border-circle">
      <thead>
        <tr>
          <th v-for="field in fields" v-bind:key="field" scope="col">{{field}}</th>
        </tr>
      </thead>
      <tbody id="app">
        <tr v-for="(stuff, index) in stuffs" v-bind:key="stuff.name" class="stuffUl">
          <th scope="row">
            <img v-bind:src="'https://'+stuff.img" class="stuffImg">
          </th>
          <td class="stuff_name">
            {{stuff.name}}
          </td>
          <td class="stuff_crystal_price">
            {{stuff.price}}
          </td>
          <td>
            <input type="number" class="price_input b-form-control"
            v-on:blur="changeInfo(index, $event)" v-on:keyup.enter="changeInfo(index, $event)" v-bind:value="stuff.priceInput">
          </td>
          <td class="origin_gold">
            {{stuff.originGold}}
          </td>
          <td class="mari_gold">
            {{stuff.mariGold}}
          </td>
          <td v-bind:class="{plus_profit : stuff.profitGold>0, minus_profit : stuff.profitGold<0}" class="profit_gold">
            {{stuff.profitGold}}
          </td>
        </tr>
        <!-- <% } %> -->
      </tbody>
    </table>
    <!-- </b-container> -->
    <!-- </div> -->
    <!-- 바로가기 -->
    
  </b-container>

</template>
<script>
  export default {
    created() {
      // 컴포넌트가 생성될 때, /api/movies에 요청을 보냅니다.
      this.$http.get('/api/mari')
        .then((response) => {
          if (200 == response.status) {
            // console.dir(response.data);
            //stuff0, stuff1, sutff2... stuff5
            var jsonObj = JSON.parse(response.data)
            console.dir(response.data);
            // var i = 0;
            for(var key in jsonObj){
              console.log(key);
              // var temp = i;
              // this.stuffs[temp] = jsonObj[key];
              // i++;
              // var tstuff = new Object();
              // tstuff[key] = jsonObj[key];
              this.stuffs.push(jsonObj[key]);
            }
              console.log(this.stuffs);
            for(var tt in this.stuffs){
              console.log(this.stuffs[tt]);
            }
            this.mariGold = 680;
          }
          else{
                console.log('response error : ' + response.status);
          }
        });
    },
    data() {
      return {
        fields: ['', '물품 명', '크리스탈', '개당 가격', '총 가격', '마리 골드', '이익'],
        stuffs: [],
        mariGold: 0
        // stuffs: {}
      }
    },
    methods:{
      /*eslint-disable no-unused-vars*/
      changeInfo :function(index, evt) {
        //개당 구매 가격 변경 시 변경사항
        console.log(index);
        console.log(this.stuffs[index]);
        var inputValue = Number(evt.target.value);
        var curStuff = this.stuffs[index];
        var temp = new Object();
        temp.img = curStuff.img;
        temp.name = curStuff.name;
        temp.price = curStuff.price;
        console.log(evt.target);
        temp.priceInput = inputValue;
        temp.originGold = this.getOriginGold(curStuff.name, inputValue);
        temp.mariGold = curStuff.mariGold;
        temp.profitGold = this.getProfitGold(temp.originGold, curStuff.mariGold);
        console.log(temp);
        this.stuffs.splice(index, 1, temp);
      },
      changeMari : function(evt){
        //화폐 거래소의 시세 변경시 변경사항
        this.mariGold = Number(evt.target.value);
        console.log("마리골드 : " + this.mariGold);
        var index = 0;
        for(var i = 0; i<6; i++){
          var temp = new Object();
          temp.img = this.stuffs[i].img;
          temp.name = this.stuffs[i].name;
          temp.price = this.stuffs[i].price;
          temp.priceInput = this.stuffs[i].priceInput;
          temp.originGold = this.stuffs[i].originGold;
          temp.mariGold = this.getMariGold(this.mariGold, this.stuffs[i].price);
          temp.profitGold = this.getProfitGold(this.stuffs[i].originGold, temp.mariGold);
          // console.log(temp);
          this.stuffs.splice(index, 1, temp);
          index++;
        }
      },

    //거래소 가격으로 구매시 골드 가격
    getOriginGold:function (name, onebyPrice) {
        var splitStrs = (name + "").trim().split(" ");
        var lastStr = splitStrs[splitStrs.length - 1].slice(1, -2).replace(',', '');
        // console.dir(lastStr + "");
        // console.dir(lastStr * onebyPrice);
        return lastStr * onebyPrice;
    },

    getProfitGold:function (origin, mari) {
        if (origin <= 0) {
            return 0;
        }
        return (origin - mari).toFixed(1);
    },

    /*
        cg    : 크리스탈의 골드가격     -> .gold_to_cash
        mcg   : 마리상점 크리스탈 가격  -> .stuff_crystal_price
        sg    : 경매장 물품 가격       -> .price_input
        stock : 상점의 물품 갯수       -> name.text()
        sol   : 이득률                -> .profit_gold
    */
    //마리에게 크리스탈로 구매시 골드 가격
    getMariGold: function (cg, mcg) {
        return ((cg / 95) * mcg).toFixed(1);
    }
    }
  }
</script>

<style lang="scss">
  @import '../assets/scss/mariStore';
  @import '../assets/scss/nav';
</style>