<template>
    <b-container fluid="lg" id="wraper">
        <!-- nav -->
        <ul class="nav nav-tabs">
            <li class="nav-item">
                <a class="nav-link" href="/">홈화면 <span class="sr-only">(current)</span></a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="#">일과기록</a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="/mari">마리상점</a>
            </li>
            <li class="nav-item">
                <a class="nav-link active" href="/collection">수집형 포인트</a>
            </li>
        </ul>

        <b-container fluid="lg" class="row div-collectionInfo">
            <div class="col-12" style="display:flex;">
                <div v-for="(menu, index) in menuTitle" v-bind:key="menu.name"
                v-on:click="changeMenu(index)"
                class="col-3 div-collection-menu" v-bind:class="{active : selectMenu == index}">{{menu.name}}</div>
            </div>
            <div class="col-6 div-collectionDocs">
                <div class="div-collectionTitle">
                    <h4>{{menuTitle[selectMenu].name}} 획득 경로</h4>
                </div>
                <div class="tab-content" id="nav-tabContent">
                    <div v-for="(collection, index) in collections[selectMenu]" v-bind:key="collection.name" class="tab-pane fade"
                    v-bind:class="{show : selectNumber == index, active : selectNumber == index}"
                    v-bind:id="'list-docs-'+index" role="tabpanel" v-bind:aria-labelledby="'list-collection-'+index">
                    설명+{{index}}
                    </div>
                    <!-- <div class="tab-pane fade" id="list-profile" role="tabpanel"
                        aria-labelledby="list-profile-list">...</div>
                    <div class="tab-pane fade" id="list-messages" role="tabpanel"
                        aria-labelledby="list-messages-list">...</div>
                    <div class="tab-pane fade" id="list-settings" role="tabpanel"
                        aria-labelledby="list-settings-list">...</div> -->
                </div>
            </div>
            <div class="col-6">
                <div class="div-collectionTitle">
                    <h4>{{menuTitle[selectMenu].name}} 획득 현황</h4>
                    <!-- <h4>거인의 심장 획득 현황</h4> -->
                </div>
                <div class="list-group div-scrollable" id="list-tab" role="tablist">
                    <div v-for="(collection, index) in collections[selectMenu]" v-bind:key="collection.name"
                    v-on:click="changeSelect(index)"
                    v-bind:class="{active : selectNumber == index,
                    complete : (selectMenu != 2 && collection.complete) || (selectMenu == 2 && collection.userCount == collection.totalCount)}"
                    class="list-group-item list-group-item-action" v-bind:id="'list-collection-'+index" data-toggle="list"
                        role="tab" v-bind:aria-controls="'list-'+index">
                        {{collection.name}}
                        <input type="checkbox" v-if="selectMenu != 2" class="input-custom-checkbox" v-model="collection.complete">
                        <div v-if="selectMenu == 2" class="div-collection-count">{{collection.userCount}}/{{collection.totalCount}}</div>
                    </div>
                        <!-- <span v-if="collection.complete">획득</span> -->
                        <!-- <span v-else>미획득</span></a> -->
                </div>
            </div>
        </b-container>


        <p style="color:white">{{collections[selectMenu]}}</p>
    </b-container>
</template>

<script>
    export default {
        created() {
            var id = '눈꽃츄르';
            // /%EB%88%88%EA%BD%83%EC%B8%84%EB%A5%B4
            this.$http.get('/api/collection/'+id)
                .then((response) => {
                    if (200 == response.status) {
                        let collectionInfo = response.data;
                        console.dir(collectionInfo);
                        for(let key in collectionInfo) {
                            console.dir(collectionInfo[key]);
                            // let jsonObj = JSON.parse(collectionInfo[key]);
                            // this.collections.push(key);
                            for(let key2 in collectionInfo[key]){
                                this.collections[key].push(collectionInfo[key][key2]);
                            }
                        }
                        // for(let key in this.collections[0]){
                        //     console.dir("collection_f>>>>" + this.collections[0][key].name + " "+ this.collections[0][key].complete);
                        // }

                    } else {
                        console.log('response error : ' + response.status);
                    }
                });
        },
        data() {
            /*eslint-disable no-unused-labels*/
            return {
                selectNumber : 0,
                selectMenu : 0,
                menuTitle: [
                    {"name":"거인의 심장"},
                    {"name":"섬의 마음"},
                    {"name":"모코코"},
                    {"name":"위대한 미술품"}
                ],
                collections: [
                    [],[],[],[]
                ]
            }
        },
        methods:{
            changeSelect(index){
                this.selectNumber = index;
            },
            changeMenu(index){
                this.selectMenu = index;
                this.selectNumber = 0;
            }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/nav';
    @import '../assets/scss/collection';
</style>